<template>
  <div>
    <el-form class="row_form" v-if="flag === '0'" ref="form" :model="form" label-width="120px">
      <el-form-item label="全部标准">
        <el-switch v-model="form.type0" @change="allChange"></el-switch>
      </el-form-item>
      <el-form-item label="国家标准">
        <el-switch v-model="form.type1" @change="itemChange"></el-switch>
      </el-form-item>
      <el-form-item label="行业标准">
        <el-switch v-model="form.type2" @change="itemChange"></el-switch>
      </el-form-item>
      <el-form-item label="地方标准">
        <el-switch v-model="form.type3" @change="itemChange"></el-switch>
      </el-form-item>
      <el-form-item label="国际标准">
        <el-switch v-model="form.type4" @change="itemChange"></el-switch>
      </el-form-item>
      <el-form-item label="欧盟法规指令">
        <el-switch v-model="form.type5"></el-switch>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit(1)">确定</el-button>
      </el-form-item>
    </el-form>
    <div v-else>
      <!-- 功能按钮 -->
      <div class="MT_fn_btn">
        <el-form :inline="true" :model="queryForm" class="btn_right demo-form-inline">
          <!--    <el-form-item label="" prop="searchType">
            <el-select v-model="queryForm.searchType" placeholder="请选择">
              <el-option label="组织机构名称" value="department"></el-option>
              <el-option label="组织机构代码" value="userCode"></el-option>
            </el-select>
          </el-form-item> -->
          <el-form-item label="组织机构代码" prop="account">
            <el-input v-model="queryForm.account" placeholder="请输入关键字搜索" clearable></el-input>
          </el-form-item>
          <el-form-item label="组织机构名称" prop="department">
            <el-input v-model="queryForm.department" placeholder="请输入关键字搜索" clearable></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click.stop="onSearch">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>
      <!-- 表格列表 -->
      <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
        <el-table-column prop="rownum_" label="序号" width="100"> </el-table-column>
        <el-table-column prop="department" label="组织机构名称"> </el-table-column>
        <el-table-column prop="type0" label="全部标准" width="150">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.type0">开启</el-tag>
            <el-tag type="danger" v-else>关闭</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="type1" label="国家标准" width="150">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.type1">开启</el-tag>
            <el-tag type="danger" v-else>关闭</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="type2" label="行业标准" width="150">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.type2">开启</el-tag>
            <el-tag type="danger" v-else>关闭</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="type3" label="地方标准" width="150">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.type3">开启</el-tag>
            <el-tag type="danger" v-else>关闭</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="type4" label="国际标准" width="150">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.type4">开启</el-tag>
            <el-tag type="danger" v-else>关闭</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="type5" label="欧盟法规法令" width="150">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.type5">开启</el-tag>
            <el-tag type="danger" v-else>关闭</el-tag>
          </template>
        </el-table-column>
        <!-- 操作 -->
        <el-table-column label="操作" width="140">
          <template slot-scope="scope">
            <el-link :underline="false" type="primary" @click.stop="edit(scope.row)">修改</el-link>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页器 -->
      <div class="MT_pagination" v-if="queryForm.total">
        <span
          class="jump_first"
          @click.stop="
            $jumpToFirst(queryForm, () => {
              loadData(1, count)
            })
          "
          >首页</span
        >
        <span
          class="jump_last"
          @click.stop="
            $jumpToLast(queryForm, () => {
              loadData(1, count)
            })
          "
          >尾页</span
        >
        <el-pagination background :total="queryForm.total" :current-page.sync="queryForm.page" :page-size.sync="queryForm.size" @current-change="loadData" layout="total, prev, pager, next, jumper" />
      </div>

      <el-dialog title="控制修改" :visible.sync="dialogVisible" width="30%">
        <el-divider></el-divider>
        <el-form class="column" ref="form" :model="form" label-width="120px">
          <el-form-item label="全部标准">
            <el-switch v-model="form.type0" @change="allChange"></el-switch>
          </el-form-item>
          <el-form-item label="国家标准">
            <el-switch v-model="form.type1" @change="itemChange"></el-switch>
          </el-form-item>
          <el-form-item label="行业标准">
            <el-switch v-model="form.type2" @change="itemChange"></el-switch>
          </el-form-item>
          <el-form-item label="地方标准">
            <el-switch v-model="form.type3" @change="itemChange"></el-switch>
          </el-form-item>
          <el-form-item label="国际标准">
            <el-switch v-model="form.type4" @change="itemChange"></el-switch>
          </el-form-item>
          <el-form-item label="欧盟法规指令">
            <el-switch v-model="form.type5"></el-switch>
          </el-form-item>
          <!-- <el-form-item>
            <el-button type="primary" @click="onSubmit">确定</el-button>
          </el-form-item> -->
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="onSubmit(0)">确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  props: ['flag'],
  data() {
    return {
      queryForm: {
        // keywords: '',
        // searchType: 'department',
        department: '', // 机构
        account: '', // 代码
        page: 1,
        size: 10,
        total: ''
      },
      form: {
        type0: 0,
        type1: 0,
        type2: 0,
        type3: 0,
        type4: 0,
        type5: 0
      },
      userId: '',
      tableData: [],
      dialogVisible: false,
      count: 1 // 初始不加载
    }
  },

  methods: {
    onSubmit(type) {
      // console.log(this.form)
      var params = this.boolean2Num(Object.assign({}, this.form))
      if (type == 0) {
        // 用户
        params.userId = this.userId
      } else {
        params.userId = 0
      }
      console.log(params)

      this.httpPost('/downControl/update', params).then(res => {
        this.$message.success('修改成功')
        this.flag !== '0' && this.loadData(1, ++this.count)
        this.dialogVisible = false
      })
    },

    edit(info) {
      this.dialogVisible = true
      console.log(info)
      this.form = this.num2Boolean({
        type0: info.type0,
        type1: info.type1,
        type2: info.type2,
        type3: info.type3,
        type4: info.type4,
        type5: info.type5
      })
      this.userId = info.id
    },

    // 搜索
    onSearch() {
      // console.log(this.queryForm)
      ++this.count
      this.loadData(1, this.count)
    },

    loadData(n, count) {
      if (n == 0) {
        let params = this.boolean2Num(this.form)
        params.userId = this.flag === '0' ? 0 : this.userId
        this.httpGet('/downControl/global/query', params).then(res => {
          var form = Object.assign({}, res)
          this.form = this.num2Boolean(form)
        })
      } else {
        if (count && count !== 1) {
          this.queryForm.total = ''
          this.httpGet('/downControl/user/query', this.queryForm).then(res => {
            // console.log(res)
            this.tableData = res.rows
            this.queryForm.total = res.total
          })
        }
      }
    },

    // 四个单个标准
    itemChange(e) {
      // console.log(e)
      if (this.form.type1 === true && this.form.type2 === true && this.form.type3 === true && this.form.type4 === true) {
        this.form.type0 = true
      } else {
        this.form.type0 = false
      }
    },

    // 所有标准
    allChange(e) {
      // console.log(e)
      if (e) {
        this.form.type1 = true
        this.form.type2 = true
        this.form.type3 = true
        this.form.type4 = true
      } else {
        this.form.type1 = false
        this.form.type2 = false
        this.form.type3 = false
        this.form.type4 = false
      }
    },
    boolean2Num(obj) {
      for (const key in obj) {
        if (obj[key] === true) {
          obj[key] = 1
        } else {
          obj[key] = 0
        }
      }
      return obj
    },
    num2Boolean(obj) {
      for (const key in obj) {
        if (obj[key] === 1) {
          obj[key] = true
        } else {
          obj[key] = false
        }
      }
      return obj
    }
  },
  watch: {
    flag(n, o) {
      console.log(n, o)
      this.loadData(n, this.count)
    }
  },
  mounted() {
    this.loadData(0)
  }
}
</script>

<style lang="scss" scoped>
.row_form {
  margin-top: 50px;
  display: flex;
}
.column {
  display: block;
}
</style>
