<template>
  <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
     <el-form-item required label="旧密码" prop="oldPass">
      <el-input type="password" v-model="ruleForm.oldPass"></el-input>
    </el-form-item>
    <el-form-item required label="新密码" prop="pass">
      <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item required label="确认密码" prop="checkPass">
      <el-input type="password" v-model="ruleForm.checkPass" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm('ruleForm')">提交</el-button>
      <el-button @click="resetForm('ruleForm')">重置</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  data() {
    // 校验旧密码
    var checkOldPass = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('请输入旧密码'))
      }
      callback()
    }

    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.ruleForm.checkPass !== '') {
          this.$refs.ruleForm.validateField('checkPass')
        }
        callback()
      }
    }

    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.ruleForm.pass) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      // 修改密码
      ruleForm: {
        oldPass: '',
        pass: '',
        checkPass: ''
      },
      rules: {
        oldPass: [{ validator: checkOldPass, trigger: 'blur' }],
        pass: [{ validator: validatePass, trigger: 'blur' }],
        checkPass: [{ validator: validatePass2, trigger: 'blur' }]
      }
    }
  },

  methods: {
    // 修改密码
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let params = {
            oldPassword:this.$md5(this.ruleForm.oldPass),
            newPassword:this.$md5(this.ruleForm.pass),
            reNewPassword:this.$md5(this.ruleForm.checkPass)
          }
          this.httpPost('/user/modPassword', params).then(res => {
            this.$message.success('修改成功,请重新登录！')
            this.httpPost('/user/logout', {}).then(res => {
              // console.log(res)
            })
            this.$sessionData('remove', 'USER')
            this.$router.replace('/login')
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 重置
    resetForm(formName) {
      this.$refs[formName].resetFields()
    }
  },

}
</script>

<style lang="scss" scoped>
.el-form {
  margin-right: 100px;
}
</style>
