<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>用户管理</el-breadcrumb-item>
        <el-breadcrumb-item>用户联系信息</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <!-- 功能按钮 -->
      <div class="MT_fn_btn">
        <el-form :inline="true" :model="queryForm" class="btn_right demo-form-inline form_border">
          <el-form-item label="组织机构名称" prop="department">
            <el-input v-model="queryForm.department" placeholder="请输入关键字搜索" clearable></el-input>
          </el-form-item>
          <el-form-item label="组织机构代码" prop="userCode">
            <el-input v-model="queryForm.userCode" placeholder="请输入关键字搜索" clearable></el-input>
          </el-form-item>
          <el-form-item label="登录名" prop="account">
            <el-input v-model="queryForm.account" placeholder="请输入关键字搜索" clearable></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click.stop="onSubmit">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>

      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns">
          <div class="btn_cont">
            <div class="choose_btn _color">
              <span @click.stop="exportData">导出当前数据</span>
            </div>
          </div>
        </div>
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="account" label="登录名" width="180"> </el-table-column>
          <el-table-column prop="userCode" label="组织机构代码" width="180"> </el-table-column>
          <el-table-column prop="department" label="机构名称"> </el-table-column>
          <!-- 0-管理员，1-普通用户，2-Key用户(组织机构)，3-Key用户的子用户，4-定制用户，5-注册用户 -->
          <el-table-column label="用户类型" width="180">
            <template slot-scope="scope">
              <span v-if="scope.row.userType == 0">管理员</span>
              <span v-if="scope.row.userType == 1">普通用户，2</span>
              <span v-if="scope.row.userType == 2">Key用户</span>
              <!-- <span v-if="scope.row.userType == 3">子用户</span> -->
              <span v-if="scope.row.userType == 4">定制用户</span>
              <span v-if="scope.row.userType == 5">注册用户</span>
            </template>
          </el-table-column>
          <el-table-column prop="extName" label="真实姓名" width="180"> </el-table-column>
          <el-table-column prop="extContract" label="联系方式" width="140"> </el-table-column>
          <el-table-column prop="extEmail" label="电子邮箱" width="180"> </el-table-column>
          <el-table-column prop="extUpdateTime" label="创建时间" width="180"> </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryForm: {
        department: '', // 机构
        userCode: '', // 代码
        account: '', // 登录名
        page: 1,
        size: 10,
        total: ''
      },
      tableData: []
    }
  },

  methods: {
    // 搜索
    onSubmit() {
      console.log(this.queryForm)
      this.loadData()
    },

    exportData() {
      let params = Object.assign({}, this.queryForm)
      let paramArray = Object.keys(params).map(key => key + '=' + params[key])
      window.open(this.$baseRequestUrl + '/user/registerUser/export?' + paramArray.join('&'))
    },

    loadData() {
      this.queryForm.total = ''
      this.httpGet('/user/contact/query', this.queryForm).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped></style>
