<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>用户管理</el-breadcrumb-item>
        <el-breadcrumb-item>注册用户管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <!-- 功能按钮 -->
      <div class="MT_fn_btn">
        <el-form :inline="true" :model="queryForm" class="btn_right demo-form-inline form_border">
          <!-- <el-form-item label="" prop="pushPlat">
            <el-select v-model="queryForm.searchType" placeholder="请选择">
              <el-option label="单位名" value="department"></el-option>
              <el-option label="登录名" value="account"></el-option>
              <el-option label="用户名" value="uusername"></el-option>
            </el-select>
          </el-form-item> -->
          <el-form-item label="登录名" prop="account">
            <el-input v-model="queryForm.account" placeholder="请输入关键字搜索" clearable></el-input>
          </el-form-item>
          <el-form-item label="用户名" prop="userName">
            <el-input v-model="queryForm.userName" placeholder="请输入关键字搜索" clearable></el-input>
          </el-form-item>
          <el-form-item label="单位名" prop="department">
            <el-input v-model="queryForm.department" placeholder="请输入关键字搜索" clearable></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click.stop="onSubmit">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>

      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns">
          <div class="btn_cont">
            <div class="choose_btn _color">
              <span @click.stop="remove">批量删除</span>
              <span @click.stop="exportData">导出当前数据</span>
            </div>
          </div>
        </div>
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="55"> </el-table-column>
          <el-table-column prop="account" label="登录名"> </el-table-column>
          <el-table-column prop="userName" label="用户名"> </el-table-column>
          <el-table-column prop="department" label="单位名称"> </el-table-column>
          <el-table-column prop="address" label="单位地址"> </el-table-column>
          <el-table-column prop="mobile" label="手机号码" width="140"> </el-table-column>
          <el-table-column prop="email" label="电子邮箱" width="180"> </el-table-column>
          <el-table-column prop="createTime" label="创建时间" width="180"> </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="230">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="resetPass(scope.row)">重置密码</el-link>
              <el-link :underline="false" type="primary" @click.stop="check(scope.row)">查看</el-link>
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)">修改</el-link>
              <el-link v-if="scope.row.flag === 1" :underline="false" type="danger" @click.stop="disabled(scope.row.id, scope.row.flag)">禁用</el-link>
              <el-link v-if="scope.row.flag === 0" :underline="false" type="primary" @click.stop="disabled(scope.row.id, scope.row.flag)">启用</el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>

      <!-- 修改 -->
      <el-dialog :title="type == 1 ? '查看' : '修改'" :visible.sync="dialogVisible" width="30%">
        <el-divider></el-divider>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="创建时间" prop="createTime">
            <el-input disabled v-model="ruleForm.createTime"></el-input>
          </el-form-item>
          <el-form-item label="登录名" prop="account">
            <el-input disabled v-model="ruleForm.account"></el-input>
          </el-form-item>
          <el-form-item label="用户名" prop="userName">
            <el-input :disabled="type === 1" v-model="ruleForm.userName"></el-input>
          </el-form-item>
          <el-form-item label="单位名称" prop="department">
            <el-input :disabled="type === 1" v-model="ruleForm.department"></el-input>
          </el-form-item>
          <el-form-item label="单位地址" prop="address">
            <el-input :disabled="type === 1" v-model="ruleForm.address"></el-input>
          </el-form-item>
          <el-form-item label="手机" prop="mobile">
            <el-input :disabled="type === 1" v-model="ruleForm.mobile"></el-input>
          </el-form-item>
          <el-form-item label="电子邮箱" prop="email">
            <el-input :disabled="type === 1" v-model="ruleForm.email"></el-input>
          </el-form-item>
          <el-form-item label="备注">
            <el-input :disabled="type === 1" type="textarea" v-model="ruleForm.memo"></el-input>
          </el-form-item>
          <!-- <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
            <el-button @click="resetForm('ruleForm')">重置</el-button>
          </el-form-item> -->
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
        </span>
      </el-dialog>

      <!-- 重置密码 -->
      <el-dialog title="重置密码" :visible.sync="dialogVisiblePass" width="40%">
        <el-divider></el-divider>
        <!-- 密码修改 -->
        <set-pass @save="save" />
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import SetPass from '@/components/set-pass/setPass'
export default {
  components: { SetPass },
  data() {
    return {
      queryForm: {
        // keywords: '',
        // searchType: 'department',
        department: '', // 机构名称
        account: '', // 登录名
        userName: '', // 用户名
        page: 1,
        size: 10,
        total: '',

        flag: [0, 1], // 是否启用，1-启用，0-禁用，2-删除
        userType: [5] // 用户类型，0-管理员，1-普通用户，2-Key用户(组织机构)，3-Key用户的子用户，4-定制用户，5-注册用户
      },
      tableData: [],
      multipleSelection: [],
      dialogVisible: false,
      ruleForm: {},
      userId: '',
      type: 1, // 1查看 2修改
      rules: {
        childUser: [{ required: true, message: '请输入子用户个数', trigger: 'blur' }],
        downLimit: [{ required: true, message: '请输入限制下载文件个数', trigger: 'blur' }]
      },
      dialogVisiblePass: false
    }
  },

  methods: {
    // 搜索
    onSubmit() {
      // this.queryForm[this.queryForm.searchType] = this.queryForm.keywords
      console.log(this.queryForm)
      this.loadData()
    },

    // 批量删除
    remove() {
      if (!this.multipleSelection.length) return this.$message.warning('请选择要删除的数据！')
      this.$confirm('此操作将删除已选中数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          var arr = []
          this.multipleSelection.map(i => arr.push(i.id))
          console.log(arr)
          this.httpPost('/role/rem', arr).then(res => {
            this.$message({ type: 'success', message: '删除成功!' })
          })
        })
        .catch(() => {})
    },

    // 复选改变
    handleSelectionChange(val) {
      this.multipleSelection = val
    },

    // 重置密码
    resetPass(id) {
      this.userId = id
      this.dialogVisiblePass = true
    },

    // 确认重置密码
    save(e) {
      var params = {
        newPassword: this.$md5(e),
        userId: this.userId
      }
      this.httpPost('/user/resetPassword', params).then(res => {
        this.dialogVisiblePass = false
        this.$message.success('修改成功')
      })
    },

    check(info) {
      this.type = 1
      this.dialogVisible = true
      this.ruleForm = Object.assign({}, info)
    },

    edit(info) {
      this.type = 2
      this.dialogVisible = true
      this.ruleForm = Object.assign({}, info)
    },

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.type === 2) {
            this.httpPost('/user/registerUser/update/' + this.ruleForm.id, this.ruleForm).then(res => {
              this.$message.success('修改成功')
              this.loadData()
            })
          }
          this.dialogVisible = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 升级
    update(id) {
      this.$confirm('是否确认此操作?', '提示', { type: 'warning' })
        .then(() => {
          this.$message({ type: 'success', message: '操作成功!' })
        })
        .catch(() => {})
    },

    // 删除
    del(id) {
      this.$delData(this, id)
    },

    // 禁用
    disabled(id, flag) {
      this.$disabledFn(this, id, flag)
    },

    // 导出数据
    exportData() {
      // /user/registerUser/export

      let params = Object.assign({}, this.queryForm)
      let paramArray = Object.keys(params).map(key => key + '=' + params[key])
      window.open(this.$baseRequestUrl + '/user/registerUser/export?' + paramArray.join('&'))
    },

    loadData() {
      this.queryForm.total = ''
      this.httpPost('/user/query', this.queryForm).then(res => {
        console.log(res)
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped></style>
