<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>用户管理</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/customUser' }">定制用户管理</el-breadcrumb-item>
        <el-breadcrumb-item>托管</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <!-- 功能按钮 -->
      <div class="MT_fn_btn">
        <el-form :inline="true" :model="queryForm" class="btn_right demo-form-inline form_border">
          <el-form-item label="标准编号" prop="code">
            <el-input v-model="queryForm.code" placeholder="请输入标准编号搜索"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click.stop="onSubmit">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>

      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns">
          <el-button type="primary" @click.stop="dialogVisible = true" icon="el-icon-plus">录入</el-button>
          <el-button type="primary" @click.stop="downModule" icon="el-icon-download">模板下载</el-button>
          <el-upload class="upload-demo" accept=".xlsx,.xls" :action="uploadAction()" :on-success="uploadSuccess" :beforeUpload="beforeExcelUpload">
            <i class="el-icon-upload"></i>
            <div class="el-upload__text"><i class="el-icon-upload2"></i> 导入</div>
          </el-upload>
          <div class="btn_cont">
            <div class="choose_btn _color">
              <span @click.stop="remove">批量删除</span>
            </div>
          </div>
        </div>
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="55"> </el-table-column>
          <el-table-column prop="rownum_" label="序号" width="100"> </el-table-column>
          <el-table-column prop="code" label="标准编号" width="200"> </el-table-column>
          <el-table-column prop="csName" label="标准名称"> </el-table-column>
          <el-table-column label="状态" width="120">
            <template slot-scope="scope">
              <!-- {{ scope.row.status === 'A' ? '现行' : '作废' }} -->
              <el-tag v-if="scope.row.status === 'A'">现行</el-tag>
              <el-tag v-else type="danger">作废</el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="replacedSd" label="被替代的标准"> </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="100">
            <template slot-scope="scope">
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>

      <!-- 录入对话框 -->

      <el-dialog title="新建录入" :visible.sync="dialogVisible" width="30%">
        <el-divider></el-divider>

        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="标准编号" prop="code">
            <el-input v-model="ruleForm.code"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">录入</el-button>
            <el-button @click="resetForm('ruleForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryForm: {
        code: '',
        page: 1,
        size: 10,
        total: ''
      },
      tableData: [],
      multipleSelection: [],
      dialogVisible: false,
      ruleForm: {
        code: ''
      },
      rules: {
        code: [{ required: true, message: '请输入标准编号', trigger: 'blur' }]
      },
      info: {}
    }
  },

  methods: {
    uploadAction() {
      // return this.$picUpload
      return this.$ExcelUpload
    },

    uploadSuccess(res, file, fileList) {
      // console.log(res, file, fileList)
      this.import(res.data.fileUrl)
    },

    import(fileUrl) {
      let params = { userId: this.info.id, fileUrl }
      console.log(params)
      this.httpPost('/standard/managed/user/import', params).then(res => {
        console.log(res)
        if (res.hasManagedStandard && res.unFindStandard) {
          this.$message({ showClose: true, type: 'warning', message: '标准号为' + res.hasManagedStandard + '的标准已存在 \n' + '标准号为' + res.unFindStandard + '的标准不存在', duration: 0 })
        } else if (res.hasManagedStandard && !res.unFindStandard) {
          this.$message({ showClose: true, type: 'warning', message: '标准号为' + res.hasManagedStandard + '的标准已存在 \n', duration: 0 })
        } else if (!res.hasManagedStandard && res.unFindStandard) {
          this.$message({ showClose: true, type: 'warning', message: '标准号为' + res.unFindStandard + '的标准不存在', duration: 0 })
        }
      })
      loadData()
    },
    beforeExcelUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 100
      if (!isLt2M) {
        this.$message({
          message: '上传文件大小不能超过 100MB!',
          type: 'warning'
        })
      }
      return isLt2M
    },

    // 模板下载
    downModule() {
      window.open(this.$templateDownloadUrl+'TEMPLATE1')
    },

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let params = Object.assign({}, this.ruleForm)
          params.userId = this.info.id
          this.httpPost('/standard/managed/user/add', params)
            .then(res => {
              this.$message({ type: 'success', message: '录入成功!' })
            })
            .catch(() => {})
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    resetForm(formName) {
      this.$refs[formName].resetFields()
    },

    del(id) {
      this.$confirm('此操作将删除数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          let params = {
            standardIdList: [id],
            userId: this.info.id
          }
          this.httpPost('/standard/managed/user/rem', params).then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.$emit('loadDate')
          })
        })
        .catch(() => {})
    },

    // 批量删除
    remove() {
      if (!this.multipleSelection.length) return this.$message.warning('请选择要删除的数据！')
      this.$confirm('此操作将删除已选中数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          let standardIdList = []
          for (var i = 0; i < this.multipleSelection.length; i++) {
            standardIdList.push(this.multipleSelection[i].id)
          }
          let params = {
            standardIdList,
            userId: this.info.id
          }
          this.httpPost('/standard/managed/user/rem', params).then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.$emit('loadDate')
          })
        })
        .catch(() => {})
    },

    handleSelectionChange(val) {
      this.multipleSelection = val
    },

    // 搜索
    onSubmit() {
      console.log(this.queryForm)
      this.loadData()
    },

    loadData() {
      this.queryForm.total = ''
      let params = Object.assign({}, this.queryForm)
      params.userId = this.info.id
      this.httpGet('/standard/managed/user/query', params).then(res => {
        console.log(res)
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    const { params } = this.$route
    if (params.info) {
      this.info = params.info
      console.log(this.info)
      this.loadData()
    } else {
      this.$router.replace('/customUser')
    }
  }
}
</script>

<style lang="scss" scoped>
/deep/.upload-demo {
  display: inline-block;
  width: 60px;
  height: 30px;
  margin-left: 10px;
  .el-upload-list {
    display: none;
  }
}
/deep/.el-upload--text {
  width: 60px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  height: 30px;
  border: 1px dashed #ccc;
  border-radius: 4px;
  .el-upload__text {
    font-size: 12px;
  }
  .el-icon-upload {
    display: none;
  }
}
</style>
