<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>用户管理</el-breadcrumb-item>
        <el-breadcrumb-item>短信用户提醒</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="短信内容" prop="desc">
          <el-input type="textarea" v-model="ruleForm.desc"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="save('ruleForm')">保存</el-button>
          <el-button type="primary" @click="submitForm('ruleForm')">发送短信</el-button>
          <el-button @click="downAddrList">下载通讯录</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ruleForm: {
        desc: ''
      },
      rules: {
        desc: [{ required: true, message: '请填写短信内容', trigger: 'blur' }]
      }
    }
  },

  methods: {
    save(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log(this.ruleForm)
          this.httpGet('/sms/save', { smsContent: this.ruleForm.desc }).then(res => {
            this.$message.success('保存成功')
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log(this.ruleForm)
          // this.httpPost('/sms/send', )
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    downAddrList() {
      // this.httpGet('/sms/userList/export', {}).then({})
      // 导出数据
      let url = '/sms/userList/export'
      window.open(this.$baseRequestUrl + url)
    },
    loadData() {
      this.httpGet('/globalConfig/query', {}).then(res => {
        console.log(res)
        this.ruleForm.desc = res.smsContent
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
/deep/.el-form-item__content {
  width: 600px;
  height: 80px;
  .el-textarea {
    height: 100%;
    .el-textarea__inner {
      height: 100%;
    }
  }
}
</style>
