<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>用户管理</el-breadcrumb-item>
        <el-breadcrumb-item>管理员管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns">
          <el-button @click.stop="add" icon="el-icon-plus">新增</el-button>
        </div>
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="rownum_" label="编号" width="100"> </el-table-column>
          <el-table-column prop="account" label="登录名"> </el-table-column>
          <el-table-column prop="userName" label="管理员姓名"> </el-table-column>
          <!-- <el-table-column prop="roleNameList" label="角色"> </el-table-column> -->
          <el-table-column label="角色">
            <template slot-scope="scope">
              <el-tag >{{ scope.row.roleNameList }}</el-tag>
              <!-- <el-tag v-for="(item, index) in scope.row.roleIdList" :key="index">{{ item }}</el-tag> -->
            </template>
          </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="200">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="resetPass(scope.row.id)">重置密码</el-link>
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)">修改</el-link>
              <el-link v-if="scope.row.flag === 1" :underline="false" type="danger" @click.stop="disabled(scope.row.id, scope.row.flag)">禁用</el-link>
              <el-link v-if="scope.row.flag === 0" :underline="false" type="primary" @click.stop="disabled(scope.row.id, scope.row.flag)">启用</el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>

      <!-- 修改 -->
      <el-dialog :title="type === 1 ? '新增管理员' : '修改管理员'" :visible.sync="dialogVisible" width="40%">
        <el-divider></el-divider>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
          <el-form-item label="登录名" prop="account">
            <el-input v-model="ruleForm.account" placeholder="请输入登录名"></el-input>
          </el-form-item>

          <el-form-item label="管理员姓名" prop="userName">
            <el-input v-model="ruleForm.userName" placeholder="请输入管理员姓名"></el-input>
          </el-form-item>

          <el-form-item v-if="type === 1" label="密码" prop="password">
            <el-input placeholder="请输入密码" v-model="ruleForm.password" show-password></el-input>
          </el-form-item>

          <!-- <el-form-item label="角色" prop="roleIdList">
            <el-checkbox-group v-model="ruleForm.roleIdList">
              <el-checkbox :label="0" :disabled="isAdmin">标准批准管理员</el-checkbox>
              <el-checkbox :label="1" :disabled="isAdmin">标准确认管理员</el-checkbox>
              <el-checkbox :label="2" :disabled="isAdmin">标准资源管理员</el-checkbox>
              <el-checkbox :label="4" :disabled="isDisabled">超级管理员</el-checkbox>
            </el-checkbox-group>
          </el-form-item> -->
          <el-form-item label="角色" prop="roleIdList">
            <el-radio-group v-model="ruleForm.roleIdList">
              <!-- <el-radio label="1">标准批准管理员</el-radio> -->
              <el-radio :label="item.id + '' " v-for="(item, index) in roleList" :key="index"> {{ item.roleName }}</el-radio>
            </el-radio-group>
          </el-form-item>
          <!-- <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
            <el-button @click="resetForm('ruleForm')">重置</el-button>
          </el-form-item> -->
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
        </span>
      </el-dialog>

      <!-- 重置密码 -->
      <el-dialog title="重置密码" :visible.sync="dialogVisiblePass" width="40%">
        <el-divider></el-divider>
        <!-- 密码修改 -->
        <set-pass @save="save" />
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import SetPass from '@/components/set-pass/setPass'
export default {
  components: { SetPass },
  data() {
    return {
      queryForm: {
        page: 1,
        size: 10,
        total: '',
        flag: [0, 1], // 是否启用，1-启用，0-禁用，2-删除
        userType: [0] // 用户类型，0-管理员，1-普通用户，2-Key用户(组织机构)，3-Key用户的子用户，4-定制用户，5-注册用户
      },
      tableData: [],
      dialogVisible: false,
      ruleForm: {
        account: '',
        userName: '',
        password: '',
        roleIdList: ''
      },
      userId: '',
      rules: {
        account: [{ required: true, message: '请输入登录名', trigger: 'blur' }],
        userName: [{ required: true, message: '请输入管理员姓名', trigger: 'blur' }],
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }],
        roleIdList: [{ required: true, message: '请选择角色', trigger: 'blur' }]
      },
      isDisabled: false,
      isAdmin: false,
      dialogVisiblePass: false,

      roleList: [],
      type: 1 // 1新增 2修改
    }
  },

  methods: {
    // 新增
    add() {
      this.type = 1
      this.dialogVisible = true
      this.$nextTick(() => {
        this.$refs.ruleForm.resetFields()
        this.ruleForm.password = ''
      })
    },

    // 搜索
    onSubmit() {
      console.log(this.queryForm)
    },

    // 重置密码
    resetPass(id) {
      this.userId = id
      this.dialogVisiblePass = true
    },

    // 确认重置密码
    save(e) {
      var params = {
        newPassword: this.$md5(e),
        userId: this.userId
      }
      this.httpPost('/user/resetPassword', params).then(res => {
        this.dialogVisiblePass = false
        this.$message.success('修改成功')
      })
    },

    edit(info) {
      console.log(info)
      this.type = 2
      this.dialogVisible = true
      this.$nextTick(() => {
        this.$refs.ruleForm.resetFields()
        this.ruleForm = {
          account: info.account,
          userName: info.userName,
          password: info.password,
          roleIdList: info.roleIdList,
          id: info.id
        }
        console.log(this.ruleForm)
      })
    },

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.ruleForm.password = this.$md5(this.ruleForm.password)
          let params = Object.assign({}, this.ruleForm)
          params.roleIdList = [this.ruleForm.roleIdList]
          console.log(params)
          if (this.type === 1) {
            this.httpPost('/user/admin/add', params)
              .then(res => {
                console.log(res)
                this.$message({ type: 'success', message: '添加成功!' })
                this.loadData()
              })
              .catch(err => {
                console.log(err)
              })
          } else {
            this.httpPost('/user/admin/update/' + params.id, params)
              .then(res => {
                console.log(res)
                this.$message({ type: 'success', message: '修改成功!' })
                this.loadData()
              })
              .catch(err => {
                console.log(err)
              })
          }
          this.dialogVisible = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 托管
    colloction(info) {
      this.$router.push({ path: '/colloction', name: 'Colloction', params: { info } })
    },

    // 删除
    del(id) {
      this.$delData(this, id)
    },

    // 禁用
    disabled(id, flag) {
      console.log(id, flag)
      this.$disabledFn(this, id, flag)
    },
    loadData() {
      this.queryForm.total = ''
      this.httpPost('/user/query', this.queryForm).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
      // 获取角色
      this.httpGet('/role/query', {}).then(res => {
        this.roleList = res.rows
      })
    }
  },

  // watch: {
  //   'ruleForm.roleIdList'(newVal, oldVal) {
  //     newVal.map(item => {
  //       if (item !== 4) {
  //         this.isDisabled = true
  //         this.isAdmin = false
  //       } else {
  //         this.isAdmin = true
  //         this.isDisabled = false
  //       }
  //     })
  //     if (newVal.length === 0) {
  //       this.isAdmin = false
  //       this.isDisabled = false
  //     }
  //   }
  // },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
.roleNameList {
  i {
    padding: 0 5px;
  }
}
.el-tag {
  margin: 0 2px;
}
.el-radio {
  margin: 8px 20px;
}
</style>
