<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>用户管理</el-breadcrumb-item>
        <el-breadcrumb-item>组织机构管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <!-- 功能按钮 -->
      <div class="MT_fn_btn">
        <el-form :inline="true" :model="queryForm" class="btn_right demo-form-inline form_border">
          <!-- <el-form-item label="" prop="searchType">
            <el-select v-model="queryForm.searchType" placeholder="请选择">
              <el-option label="组织机构名称" value="department"></el-option>
              <el-option label="组织机构代码" value="userCode"></el-option>
            </el-select>
          </el-form-item> -->
          <el-form-item label="组织机构代码" prop="userCode">
            <el-input v-model="queryForm.userCode" placeholder="请输入关键字搜索" clearable></el-input>
          </el-form-item>
          <el-form-item label="组织机构名称" prop="department">
            <el-input v-model="queryForm.department" placeholder="请输入关键字搜索" clearable></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click.stop="onSubmit">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>

      <div class="MT_table_cont">
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="userCode" label="机构代码" width="200"> </el-table-column>
          <el-table-column prop="department" label="机构名称"> </el-table-column>
          <el-table-column prop="deptType" label="机构类型" width="140"> </el-table-column>
          <el-table-column prop="legalPerson" label="机构法人" width="140"> </el-table-column>
          <el-table-column prop="address" label="机构地址"> </el-table-column>
          <el-table-column label="子用户限制" width="100">
            <template slot-scope="scope">
              <span class="choose_btn _color" onmouseover="this.style.fontWeight='bold'" onmouseout="this.style.fontWeight='normal'" style="cursor: pointer" @click="childNum(scope.row)">
                {{ scope.row.childNumber }}
              </span>
            </template>
          </el-table-column>
          <el-table-column label="下载限制" width="100">
            <template slot-scope="scope">
              <span class="choose_btn _color" onmouseover="this.style.fontWeight='bold'" onmouseout="this.style.fontWeight='normal'" style="cursor: pointer" @click="downloadLimit(scope.row)">
                {{ scope.row.downloadLimit }}
              </span>
            </template>
          </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="220">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="check(scope.row)">查看子用户</el-link>
              <el-link :underline="false" type="primary" @click.stop="update(scope.row.id)">升级</el-link>
              <el-link v-if="scope.row.flag === 1" :underline="false" type="danger" @click.stop="disabled(scope.row.id, scope.row.flag)">禁用</el-link>
              <el-link v-if="scope.row.flag === 0" :underline="false" type="primary" @click.stop="disabled(scope.row.id, scope.row.flag)">启用</el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>

      <!-- 修改 -->
      <el-dialog :title="label + '修改'" :visible.sync="dialogVisible" width="30%">
        <el-divider></el-divider>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
          <el-form-item v-if="label === '子用户'" label="子用户个数" prop="childNumber">
            <el-input v-model="ruleForm.childNumber"></el-input>
          </el-form-item>

          <el-form-item v-else label="下载文件个数" prop="downloadLimit">
            <el-input v-model="ruleForm.downloadLimit"></el-input>
          </el-form-item>

          <!-- <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
            <el-button @click="resetForm('ruleForm')">重置</el-button>
          </el-form-item> -->
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryForm: {
        // keywords: '',
        // searchType: 'department',
        department: '', // 机构
        userCode: '', // 代码
        page: 1,
        size: 10,
        total: '',

        flag: [0, 1], // 是否启用，1-启用，0-禁用，2-删除
        userType: [2] // 用户类型，0-管理员，1-普通用户，2-Key用户(组织机构)，3-Key用户的子用户，4-定制用户，5-注册用户
      },
      tableData: [],
      dialogVisible: false,
      ruleForm: {
        childNumber: '',
        downloadLimit: ''
      },
      label: '', // 当前选中的 子用户/下载限制
      activeId: '', // 当前选种的Id
      rules: {
        childNumber: [{ required: true, message: '请输入子用户个数', trigger: 'blur' }],
        downloadLimit: [{ required: true, message: '请输入限制下载文件个数', trigger: 'blur' }]
      }
    }
  },

  methods: {
    // 搜索
    onSubmit() {
      // this.queryForm[this.queryForm.searchType] = this.queryForm.keywords
      console.log(this.queryForm)
      this.loadData()
    },

    // 查看子用户
    check(info) {
      this.$router.push({ path: '/childUser', name: 'ChildUser', params: { info, type: 1 } })
      this.$sessionData('set', 'type', 1)
      this.$sessionData('set', 'childInfo', info)
    },

    // 子用户修改
    childNum(row) {
      console.log(row)
      this.activeId = row.id
      this.ruleForm.childNumber = row.childNumber
      this.label = '子用户'
      this.dialogVisible = true
    },

    // 下载限制修改
    downloadLimit(row) {
      this.activeId = row.id
      this.label = '下载个数'
      this.ruleForm.downloadLimit = row.downloadLimit
      this.dialogVisible = true
    },

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          if (this.label === '子用户') {
            var params = {
              childNumber: this.ruleForm.childNumber,
              userId: this.activeId
            }
            this.httpPost('/user/childNumber/update', params)
              .then(res => {
                console.log(res)
                this.loadData()
              })
              .catch(err => {})
          } else {
            var params = {
              downloadLimit: this.ruleForm.downloadLimit,
              userId: this.activeId
            }
            this.httpPost('/user/downloadLimit/update', params)
              .then(res => {
                console.log(res)
                this.loadData()
              })
              .catch(err => {})
          }
          this.dialogVisible = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    // 升级
    update(id) {
      var params = {
        userId: id,
        userType: 4
      }
      this.$confirm('是否确认升级操作?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/user/userType/update/' + id, {})
            .then(res => {
              this.loadData()
              this.$message({ type: 'success', message: '升级成功!' })
            })
            .catch(err => {
              console.log(err)
            })
        })
        .catch(() => {})
    },

    // 删除
    del(id) {
      this.$delData(this, id)
    },

    // 禁用
    disabled(id, flag) {
      console.log(id, flag)
      this.$disabledFn(this, id, flag)
    },

    loadData() {
      this.queryForm.total = ''
      this.httpPost('/user/query', this.queryForm).then(res => {
        console.log(res)
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
.choose_btn {
  cursor: pointer;
  text-decoration: underline;
  color: #409eff;
}
</style>
