<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>用户管理</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: type === '1' ? '/organization' : '/customUser' }">{{ type === '1' ? '组织机构管理' : '定制用户管理' }}</el-breadcrumb-item>
        <el-breadcrumb-item>子用户管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns" v-if="type === '2'">
          <el-button @click.stop="add" icon="el-icon-plus">新建</el-button>
        </div>
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="account" label="登录名" width="200"> </el-table-column>
          <el-table-column prop="extName" label="姓名" width="200"> </el-table-column>
          <el-table-column prop="department" label="机构名称"> </el-table-column>
          <el-table-column prop="deptType" label="状态" width="100">
            <template slot-scope="scope">
              <!-- {{ scope.row.flag === 1 ? '启用' : '禁用' }} -->
              <el-tag v-if="scope.row.flag === 1">启用</el-tag>
              <el-tag type="danger" v-else>禁用</el-tag>
            </template>
          </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="220">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="check(scope.row.id)">查看日志</el-link>
              <el-link :underline="false" type="primary" @click.stop="resetPass(scope.row.id)">重置密码</el-link>
              <el-link v-if="scope.row.flag === 1" :underline="false" type="danger" @click.stop="disabled(scope.row.id, scope.row.flag)">禁用</el-link>
              <el-link v-if="scope.row.flag === 0" :underline="false" type="primary" @click.stop="disabled(scope.row.id, scope.row.flag)">启用</el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span
            class="jump_first"
            @click.stop="
              $jumpToFirst(queryForm, () => {
                loadData(type)
              })
            "
            >首页</span
          >
          <span
            class="jump_last"
            @click.stop="
              $jumpToLast(queryForm, () => {
                loadData(type)
              })
            "
            >尾页</span
          >
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="
              () => {
                loadData(type)
              }
            "
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>

      <!-- 查看日志 -->
      <el-dialog title="日志信息" :visible.sync="dialogVisible" width="60%">
        <el-divider></el-divider>
        <!-- 功能按钮 -->
        <div class="MT_fn_btn">
          <el-form :inline="true" :model="queryForm2" class="btn_right demo-form-inline">
            <el-form-item label="" prop="pushPlat">
              <el-select v-model="queryForm2.logType" placeholder="请选择">
                <el-option label="全部" value=""></el-option>
                <el-option label="登录" value="1"></el-option>
                <el-option label="退出" value="2"></el-option>
                <el-option label="查看" value="3"></el-option>
                <el-option label="下载" value="4"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click.stop="onSubmit">搜索</el-button>
            </el-form-item>
          </el-form>
        </div>

        <!-- 表格列表 -->
        <el-table border ref="multipleTable" :data="tableData2" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="userName" label="操作人" width="200"> </el-table-column>
          <el-table-column prop="ipAddress" label="IP地址" width="200"> </el-table-column>
          <el-table-column label="类型" width="100">
            <template slot-scope="scope">
              <span v-if="scope.row.type === 1">登录</span>
              <span v-if="scope.row.type === 2">退出</span>
              <span v-if="scope.row.type === 3">查看</span>
              <span v-if="scope.row.type === 4">下载</span>
            </template>
          </el-table-column>
          <el-table-column prop="content" label="动作"> </el-table-column>
          <el-table-column prop="updateTime" label="时间" width="180"> </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm2.total">
          <span
            class="jump_first"
            @click.stop="
              $jumpToFirst(queryForm2, () => {
                loadData(type, 1)
              })
            "
            >首页</span
          >
          <span
            class="jump_last"
            @click.stop="
              $jumpToLast(queryForm2, () => {
                loadData(type, 1)
              })
            "
            >尾页</span
          >
          <el-pagination
            background
            :total="queryForm2.total"
            :current-page.sync="queryForm2.page"
            :page-size.sync="queryForm2.size"
            @current-change="
              () => {
                loadData(type, 1)
              }
            "
            layout="total, prev, pager, next, jumper"
          />
        </div>
        <span slot="footer" class="dialog-footer">
          <!-- <el-button @click="dialogVisible = false">取 消</el-button> -->
          <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
        </span>
      </el-dialog>

      <!-- 重置密码 -->
      <el-dialog title="重置密码" :visible.sync="dialogVisiblePass" width="40%">
        <el-divider></el-divider>
        <!-- 密码修改 -->
        <set-pass @save="save" />
      </el-dialog>

      <!-- 新建子用户 -->
      <el-dialog title="新建子用户" :visible.sync="dialogVisibleChild" width="30%">
        <el-divider></el-divider>

        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="登录名" prop="account">
            <el-input v-model="ruleForm.account"></el-input>
          </el-form-item>
          <el-form-item label="用户姓名" prop="username">
            <el-input v-model="ruleForm.username"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input type="password" v-model="ruleForm.password" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="确认密码" prop="confirmPassword">
            <el-input type="password" v-model="ruleForm.confirmPassword" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="单位名称" prop="department">
            <el-input v-model="ruleForm.department"></el-input>
          </el-form-item>
          <el-form-item label="联系电话" prop="phone">
            <el-input v-model="ruleForm.phone"></el-input>
          </el-form-item>
          <el-form-item label="联系地址" prop="address">
            <el-input v-model="ruleForm.address"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
            <el-button @click="resetForm('ruleForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import SetPass from '@/components/set-pass/setPass'
export default {
  components: { SetPass },
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.ruleForm.confirmPassword !== '') {
          this.$refs.ruleForm.validateField('confirmPassword')
        }
        callback()
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.ruleForm.password) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      type: '1', //  1组织机构  2定制用户
      info: {}, // 上个页面的用户信息
      queryForm: {
        page: 1,
        size: 10,
        total: '',

        flag: [0, 1], // 是否启用，1-启用，0-禁用，2-删除
        userType: [2] // 用户类型，0-管理员，1-普通用户，2-Key用户(组织机构)，3-Key用户的子用户，4-定制用户，5-注册用户
      },
      queryForm2: {
        logType: '', // ''全部 1登录 2退出 3查看 4下载
        page: 1,
        size: 10,
        total: ''
      },
      tableData: [],
      tableData2: [],
      dialogVisible: false,
      dialogVisiblePass: false,
      dialogVisibleChild: false,
      ruleForm: {
        account: '',
        username: '',
        password: '',
        confirmPassword: '',
        department: '',
        address: '',
        phone: ''
      },
      rules: {
        account: [{ required: true, message: '请输入登录名', trigger: 'blur' }],
        username: [{ required: true, message: '请输入用户姓名', trigger: 'blur' }],
        password: [{ required: true, validator: validatePass, trigger: 'blur' }],
        confirmPassword: [{ required: true, validator: validatePass2, trigger: 'blur' }],
        department: [{ required: true, message: '请输入单位名称', trigger: 'blur' }]
      },
      userId: ''
    }
  },

  methods: {
    //  子用户-----------------------------------------------------------------

    add() {
      this.dialogVisibleChild = true
    },

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          var params = Object.assign({}, this.ruleForm)
          params.password = params.confirmPassword = this.$md5(this.ruleForm.password)
          params.userId = this.info.id
          console.log(params)

          this.httpPost('/user/child/add', params)
            .then(res => {
              console.log(res)
              this.$message.success('添加成功')
              this.loadData(this.type)
            })
            .catch(err => {})
          this.dialogVisibleChild = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },

    //  子用户-----------------------------------------------------------------

    // 搜索
    onSubmit() {
      console.log(this.queryForm2)
      this.loadData(this.type, 1)
    },

    // 查看日志
    check(id) {
      this.userId = id
      this.queryForm2.logType = ''
      this.dialogVisible = true
      this.loadData(this.type, 1)
    },

    // 重置密码
    resetPass(id) {
      this.userId = id
      this.dialogVisiblePass = true
    },

    // 确认重置密码
    save(e) {
      var params = {
        newPassword: this.$md5(e),
        userId: this.userId
      }
      this.httpPost('/user/resetPassword', params).then(res => {
        this.dialogVisiblePass = false
        this.$message.success('修改成功')
      })
    },

    // 禁用
    disabled(id, flag) {
      console.log(id, flag)
      this.$disabledFn(this, id, flag)
    },

    // 删除
    del(id) {
      this.$delData(this, id)
    },

    // type 1组织机构 2定制用户
    loadData(type, log) {
      if (log) {
        this.queryForm2.total = ''
        let params = Object.assign({}, this.queryForm2)
        params.userType = 1
        params.userId = this.userId
        this.httpGet('/log/user/query', params).then(res => {
          this.tableData2 = res.rows
          this.queryForm2.total = res.total
        })
      } else {
        this.queryForm.total = ''
        let params = Object.assign({}, this.queryForm)
        params.userType = [3]
        params.keyUser = this.info.id
        this.httpPost('/user/query', params).then(res => {
          this.tableData = res.rows
          this.queryForm.total = res.total
        })
      }
    }
  },

  mounted() {
    this.type = this.$sessionData('get', 'type')
    this.info = this.$sessionData('get', 'childInfo')
    this.loadData(this.type)
  }
}
</script>

<style lang="scss" scoped>
.el-form {
  margin-right: 100px;
}
</style>
