<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>用户管理</el-breadcrumb-item>
        <el-breadcrumb-item>角色管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns">
          <el-button @click.stop="add" icon="el-icon-plus">新增</el-button>
        </div>
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
          <el-table-column prop="rownum_" label="编号" width="100"> </el-table-column>
          <el-table-column prop="roleName" label="角色名称"> </el-table-column>
          <el-table-column prop="roleDesc" label="角色描述"> </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="100">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)">修改</el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>

      <!-- 新增角色 -->
      <el-dialog :title="type === 1 ? '新增角色' : '修改角色'" :visible.sync="dialogVisible" width="60%">
        <el-divider></el-divider>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
          <el-form-item label="角色名称" prop="roleName">
            <el-input v-model="ruleForm.roleName"></el-input>
          </el-form-item>

          <el-form-item label="角色描述" prop="roleDesc">
            <el-input v-model="ruleForm.roleDesc"></el-input>
          </el-form-item>

          <el-form-item class="right" label="角色权限" prop="roleRight">
            <el-tree ref="tree" :data="rightList" show-checkbox node-key="id" @check="checkChange" :default-checked-keys="checkList" :props="defaultProps" default-expand-all check-on-click-node>
            </el-tree>
          </el-form-item>

          <!-- <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
            <el-button @click="resetForm('ruleForm')">重置</el-button>
          </el-form-item> -->
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="submitForm('ruleForm')">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import menuList from '@/utils/menu'
export default {
  data() {
    return {
      queryForm: {
        keywords: '',
        searchType: '0',
        page: 1,
        size: 10,
        total: ''
      },
      tableData: [],
      dialogVisible: false,
      ruleForm: {
        roleName: '',
        roleDesc: ''
      },
      rules: {
        roleName: [{ required: true, message: '请输入角色名称', trigger: 'blur' }]
      },
      rightList: [],
      defaultProps: {
        children: 'childNode',
        label: 'name'
      },
      checkList: [], // 得到的数据
      checkListStr: '', //  已改变的数据
      roleId: '',
      type: 1 // 1增 2改
    }
  },

  methods: {
    // 权限选择变化回调监听
    checkChange(currNode, checkedObj) {
      // console.log(checkedObj.checkedKeys)
      this.checkList = checkedObj.checkedKeys
      this.checkListStr = this.roleJoin(checkedObj.checkedKeys)
    },

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          // console.log(this.ruleForm)
          var params = {
            roleAct: this.checkListStr || this.ruleForm.roleAct,
            roleDesc: this.ruleForm.roleDesc,
            roleName: this.ruleForm.roleName
          }
          console.log(params)
          if (this.type === 1) {
            // 新增
            this.httpPost('/role/add', params)
              .then(res => {
                this.$message.success('保存成功')
                this.loadData()
              })
              .catch(err => {})
          } else {
            // 修改
            this.httpPost('/role/mod/' + this.roleId, params).then(res => {
              this.$message.success('保存成功')
              this.loadData()
            })
          }
          this.dialogVisible = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },

    add() {
      this.type = 1
      this.dialogVisible = true
      this.$nextTick(() => {
        this.$refs.tree.setCheckedKeys([])
        this.$refs.ruleForm.resetFields()
      })
    },

    edit(info) {
      this.type = 2
      this.roleId = info.id
      this.dialogVisible = true
      // 请求数据
      this.$nextTick(() => {
        this.$refs.tree.setCheckedKeys(this.roleMat(info.roleAct))
        this.ruleForm = Object.assign({}, info)
      })
    },

    del(id) {
      this.$confirm('此操作将删除数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/role/rem', [id]).then(res => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },

    loadData() {
      this.queryForm.total = ''
      this.httpGet('/role/query', {}).then(res => {
        // console.log(res)
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    },
    roleJoin(menuArr) {
      var str = menuArr
      var tempArr = []
      var numList = [] // 一级数字
      // console.log(str)
      menuArr.map(item => {
        if (item.length > 1) {
          numList.push(item.slice(0, 1))
          tempArr.push(item.slice(0, 1) + '_' + item)
        }
      })
      numList = this.quChongfn(numList)
      var str = tempArr.concat(numList).join(',')
      str = str.replace(/,/g, '|')

      return str
    },

    roleMat(menuStr) {
      var nav = Object.assign([], this.menuArr)
      nav.splice(0, 1) // 菜单
      // console.log(nav)
      // console.log(menuStr)
      var arr = menuStr.split('|')
      var tempArr = [] // 转换后要显示的数字组
      var numList = [] // 转换后的二维数组
      var needDel = [] // 需要删除的一级数字
      arr.map(item => {
        if (item.indexOf('_') === -1) {
          // 一级菜单
          tempArr.push(item)
          numList.push({ a: item, children: [] }) //  拿到一级并生成二级
        } else {
          numList.map(n => {
            // 遍历 放入二级
            if (n.a == item.split('_')[0]) {
              n.children.push(item.split('_')[1])
            }
          })
          tempArr.push(item.split('_')[1])
        }
      })
      numList.map((i, l) => {
        nav.map((j, k) => {
          if (i.children.length < j.childNode.length) {
            needDel.push(i.a)
          }
        })
      })
      // 去重
      needDel = this.quChongfn(needDel)
      // console.log(numList)
      // console.log(needDel)

      needDel.map((item, index) => {
        var i = tempArr.findIndex(l => l == item)
        tempArr.splice(i, 1)
      })
      // console.log(tempArr)
      this.checkList = tempArr
      return tempArr
    },

    quChongfn(arr) {
      var newArr = []
      for (let i = 0; i < arr.length; i++) {
        var n = arr[i]
        if (newArr.indexOf(n) == -1) {
          newArr.push(n)
        }
      }
      return newArr
    }
  },

  mounted() {
    this.loadData()
    const menu = Object.assign([], menuList)
    this.menuArr = Object.assign([], menuList)
    menu.splice(0, 1)
    this.rightList = menu
  }
}
</script>

<style lang="scss" scoped>
.el-form-item:not(.right) {
  /deep/.el-form-item__content {
    width: 400px;
  }
}
/deep/.el-tree {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  & > .el-tree-node {
    width: 200px;
    padding: 20px;
  }
}
</style>
